import { handleErrors } from './index';

export const loginUser = async ({email, password}) => {
  return await fetch('https://mysterious-reef-29460.herokuapp.com/api/v1/validate', {
      method: 'POST',
      // credentials: 'include',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        email,
        password
      })
    })
      .then(handleErrors);
}

export const getUserProfile = async (id) => {
  return await fetch(`https://mysterious-reef-29460.herokuapp.com/api/v1/user-info/${id}`, {
      method: 'GET',
      // credentials: 'include',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      }
    })
      .then(handleErrors);
}



