import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import actions from '../../actions';
import Loading from '../../hoc/Loading';
import Message from '../../components/Message';

import styles from './index.module.css';

class Profile extends Component {
  componentWillMount = () => {
    if (!this.props.profile) {
      this.props.userGetProfile();
    }
  }
  render() {
    const {profile, error} = this.props;
    return (
      <div>
        <section className={ styles.main }>
          { !error && profile && !!profile.payload && <div className={ styles.profile }>
                                                        <div>
                                                          <b>Город</b>:
                                                          { profile.payload.city }
                                                        </div>
                                                        <br/>
                                                        <div>
                                                          <b>Знание языков:</b>
                                                          <ul>
                                                            { profile.payload.languages.map(i => (<li key={ i }>
                                                                                                    { i }
                                                                                                  </li>)) }
                                                          </ul>
                                                        </div>
                                                        <div>
                                                          <b>Ссылки:</b>
                                                          <ul>
                                                            { profile.payload.social.map(i => (<li key={ i.label }>
                                                                                                 <a href={ i.link }> <i className={ i.icon }></i>
                                                                                                   <span>{ i.label }</span> </a>
                                                                                               </li>)) }
                                                          </ul>
                                                        </div>
                                                      </div> }
        </section>
        { error && <Message type='error' text={ `${profile.payload}` } /> }
      </div>
      );
  }
}

Profile.propsTypes = {
  profile: PropTypes.object
}

Profile = Loading(Profile);

Profile = connect(state => ({
  profile: state.user.profile,
  error: state.user.profile && state.user.profile.error,
  loading: state.user.profile && state.user.profile.loading
}), {
  userGetProfile: actions.userGetProfile
})(Profile)

export default Profile;


