const initialState = {};

export default (state = {
    payload: initialState
  }, action) => {
  switch (action.type) {
    case 'USER/LOGIN':
      return {
        error: action.error,
        loading: action.loading,
        ...action.payload
      }
    case 'USER/LOGOUT':
      return {}
    case 'USER/SET_PROFILE':
      return {
        ...state,
        profile: {
          error: action.error,
          loading: action.loading,
          ...action.payload
        }
      }
    default:
      return state
  }
}


