import { handleErrors } from './index';

export const getNews = async () => {
  return await fetch('https://mysterious-reef-29460.herokuapp.com/api/v1/news', {
      method: 'GET',
      // credentials: 'include',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      }
    })
      .then(handleErrors);
}


