import { defaultAction } from './index';
import api from '../api';

export const userSet = (data) => defaultAction('USER/LOGIN', data);

export const userLogin = (data) => async (dispatch, getState) => {
  const {email, password} = data;
  dispatch(userSet({
    loading: true
  }));
  try {
    const user = await api.loginUser({
      email,
      password
    });
    dispatch(userSet({
      payload: user
    }));
  } catch (error) {
    console.error(error);
    dispatch(userSet({
      error: true,
      payload: error
    }));
  }
};

export const userLogout = () => defaultAction('USER/LOGOUT');

export const userSetProfile = (data) => defaultAction('USER/SET_PROFILE', data);

export const userGetProfile = () => async (dispatch, getState) => {
  dispatch(userSetProfile({
    loading: true
  }));
  try {
    const profile = await api.getUserProfile(getState().user.payload.id);
    // update profile
    profile.social = profile.social.map(i => {
      let icon;
      if (i.label === 'web') {
        icon = `fas fa-globe`
      } else {
        icon = `fab fa-${i.label}`
      }
      return {
        ...i,
        icon
      }
    });
    profile.social.sort((a, b) => a.label === 'web' ? -1 : a.label < b.label);
    // ===============================
    dispatch(userSetProfile({
      payload: profile
    }));
  } catch (error) {
    console.error(error);
    dispatch(userSetProfile({
      error: true,
      payload: error
    }));
  }
};


