import { defaultAction } from './index';
import api from '../api';

export const newsSet = (data) => defaultAction('NEWS/SET', data);

export const newsGet = () => async (dispatch, getState) => {
  let news = [];
  dispatch(newsSet({
    loading: true
  }));
  try {
    news = await api.getNews();
    dispatch(newsSet({
      payload: news
    }));
  } catch (error) {
    console.error(error);
    dispatch(newsSet({
      error: true,
      payload: error
    }));
  }
};


